export default{
  data() {
    return {
      style: 0,                    // 商品块默认0上图下文，1为左图右文
      searchInfo: '',              // 输入框placeholdeer
      inputClearValue: '',        //  输入框value值
      showClearIcon: false,       // 输入框清空
      tabIndex: 0,                // 默认选中分享
      filterIndex: 0,             // 默认选中综合
      tabList: ['分享', '商城'],
      shareTag: [{tag_name:'综合'}, {tag_name:'最热'}, {tag_name:'最新'}, {tag_name:'官方'}, {tag_name:'筛选'}],   // 标签默认这个是商品标签
      filter: ['品牌', '分类'],
      filterTag_Index: '',            //默认选中品牌
      filter_alert: true,             // 筛选遮罩层显示
      shareList: [],
      filterCoverList: {
        list: ['铁三角', '索尼', '铁三角', '索尼', '铁三角'],
        sum: 4999
      },
      filterArr: [],
      goodList: [],                                  // 商城数据
      captionList: [
        {
          title: '品牌',
          selectIndexArr: ['默认'],               //循环时加上
          arr: []
        }, {
          title: '分类',
          selectIndexArr: ['默认'],
          arr: []
        }, {
          title: '促销',
          selectIndexArr: ['默认'],
          arr: []
        }
      ],                             // 筛选侧边栏数据
      selectArr: [],                  // 筛选侧边栏展开的数组index
    }
  },
  watch: {
    tabIndex(val, oldval) {
      this.filterIndex = 0
      this.searchAction(this.searchInfo, this.tabIndex)
      if(val === 0) {
        this.getCultureTag()
        return
      }
      if(val === 1) {
        this.shareTag = [{tag_name:'综合'}, {tag_name:'最热'}, {tag_name:'最新'}, {tag_name:'官方'}, {tag_name:'筛选'}]
        return
      }
    },
    filterIndex(val, oldVal) {
      if(val === 4) {
        this.filter_alert = true
      }
    }
  },
  onLoad(option) {
    console.log('分享文章详情页接受到的参数',option.class)
    this.searchInfo = option.class
    this.tabIndex = Number(option.type)
    this.searchAction(option.class, this.tabIndex)
    // 获取文章标签
    if(this.tabIndex === 0) {
      this.getCultureTag()
    }
  },
  methods: {
    // 选中分享文章标签
    getCultureTag() {
      this.$http({
        url: this.$api.activitytags,
        cb: (err, res) => {
          // console.log(res.data.tags)
          this.shareTag = res.data.tags
        }
      })
    },
    
    // 搜索typeCode 0 为分享 1为商品
    searchAction(info, typeCode) {
      let url = this.$api.goodlists
      let data = {
        category_id: '',
        search: info,
        sortType: '',
        sortPrice: '',
        listRows: '',
        brand_id: '',
        promotions_type: '',
        min_price: '',
        max_price: ''
      }
      if( typeCode === 0 ) {
        url= this.$api.articlesbysearch
        data = {
          search: info,
          tags_id: ''
        }
      }
      this.$http({
        url: url,
        data: data,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            // 成功后刷新数据
            if(res.data.list.length < 1) {
              uni.showToast({
              	title: '未搜索到相关数据',
                icon: 'none'
              })
              return
            }
            switch (typeCode) {
              case 0:
                this.shareList = res.data.list
                break
              case 1:
                this.goodList = res.data.list.data
                break 
            }
          } else {
            uni.showToast({
            	title: '搜索失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 切换商品排列样式
    changeStyle() {
      if(this.style === 0) {
        this.style = 1
        return
      }
      this.style = 0
    },
    // 返回
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    clearIcon() {
    	this.inputClearValue = ''
    	this.showClearIcon = false
    },
    // 分享详情页
    goShareDetail(id) {
      uni.navigateTo({
        url: '../components/shareInfo?article_id=' + id
      })
    },
    // 
    clearInput(event) {
      console.log(event.target.value)
    	this.inputClearValue = event.target.value
    	if (event.target.value.length > 0) {
    		this.showClearIcon = true
        return
    	} else {
    		this.showClearIcon = false
    	}
    },
    // 分享商城tab点击
    selectTab(index) {
      this.tabIndex = index
    },
    // 价格等分类点击
    selectFilter(index) {
      if(!this.filter_alert || index === 4) {
        // // 给captionList加上一个选中的索引空数组selectIndexArr
        
        // 品牌分类
        this.$http({
          url: this.$api.getbrands,
          cb: (err, res) => {
            let arr = []
            let list = res.data.list
            for(let item in list) {
              arr.push(list[item])
            }
            this.captionList[0].arr = arr
          }
        })
        // 商品分类
        this.$http({
          url: this.$api.goodscategory,
          cb: (err, res) => {
            console.log(res.data.list)
            this.captionList[1].arr = res.data.list
          }
        })
        // 促销活动
        this.$http({
          url: this.$api.promotions,
          cb: (err, res) => {
            // console.log(res.data.promotions)
            this.captionList[2].arr = res.data.promotions
          }
        })
        
        this.filter_alert = true
      }
      this.filterIndex = index
    },
    //直接点击外面的分类品牌
    selectFilterTag(info) {
      let index = info.toString()
      if(index === this.filterTag_Index && this.filterTag_Index !== '') {
        this.filterTag_Index = ''
        return
      }
      if (index !== this.filterTag_Index || this.filterTag_Index === '') {
        this.filterTag_Index = index
      }
    },
    // 点击筛选侧边栏中的品牌，活动等分类
    setCategory(index) {
      if(this.selectArr.indexOf(index) === -1) {
        this.selectArr.push(index)
        return
      }
      this.selectArr.splice(this.selectArr.indexOf(index), 1)
    },
    // 筛选侧边弹窗选择分类里面的子选项
    selTag(index, num) {
      let name = this.captionList[index].arr[num]
      let charIndex = this.captionList[index].selectIndexArr.indexOf(name)
      if(charIndex === -1) {
        this.captionList[index].selectIndexArr.push(name)
        return
      }
      if (charIndex !== -1) {
        this.captionList[index].selectIndexArr.splice(charIndex, 1)
        return
      }
    },
    // 重置筛选
    resetFilter() {
      this.captionList.map((item, index) => {
        item.selectIndexArr = ['默认']
      })
    },
    clickZan(item, index) {
      console.log(item.article_id, item.islike, index)
      let url = this.$api.unLike
      if(item.islike === 'no') {
        url = this.$api.like
      }
      this.$http({
        url: url,
        data: {
          article_id: item.article_id
        },
        cb: (err, res) => {
          if(!err && res) {
            switch(this.shareList[index].islike) {
              case 'yes':
                uni.showToast({
                  title: '取消点赞成功',
                  icon: 'none'
                })
                this.shareList[index].islike = 'no'
                this.shareList[index].like_count -= 1
                break
              case 'no':
                uni.showToast({
                  title: '点赞成功',
                  icon: 'none'
                })
                this.shareList[index].islike = 'yes'
                this.shareList[index].like_count += 1
                break
            }
          } else {
            switch(this.shareList[index].islike) {
              case 'yes':
                uni.showToast({
                	title: '取消点赞失败',
                  icon: 'none'
                })
                break
              case 'no':
                uni.showToast({
                	title: '点赞失败请重试',
                  icon: 'none'
                })
                break
            }
          }
        }
      })
      // if (!this.shareList[index].zan_status) {
      //   this.shareList[index].zan_num += 1
      //   this.shareList[index].zan_status = !this.shareList[index].zan_status
      //   return
      // }
      // if (this.shareList[index].zan_status) {
      //   this.shareList[index].zan_num -= 1
      //   this.shareList[index].zan_status = !this.shareList[index].zan_status
      //   return
      // }
    },
    goDetail(item) {
      uni.navigateTo({
        url: '../components/goodDetail?info=' + item.name
      })
    }
  }
}
